#home {
  position: relative;
  background: url("../../assets/bgIMG.png");
  background-size: cover;
  background-position: center;
  background-repeat: repeat;

  .app__wrapper {
    padding: 0;

    .copyright {
      display: none;
    }
  }
}

.app__header {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
  padding: 6rem 2rem 0rem;

  @media screen and (min-width: 2000px) {
    padding-top: 8rem;
  }

  @media screen and (max-width: 1200px) {
    .p-text{
      font-size: small;
    }
    .head-text{
      font-size: 1rem;
    }
    flex-direction: column;
  }

  @media screen and (max-width: 450px) {
    padding: 6rem 1rem 2rem;
  }
}

.app__header-info {
  flex: 0.65;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;
  margin: 0 2rem;

  @media screen and (max-width: 1200px) {
    width: 100%;
    margin-right: 0;
  }

  @media screen and (max-width: 450px) {
    margin: 0 1rem;
  }
}

.app__header-badge {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;

  .badge-cmp,
  .tag-cmp {
    padding: 0.5rem 1rem; // Reduced padding for smartphones
    background: var(--white-color);
    border-radius: 15px;
    flex-direction: row;
    width: auto;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  }

  .tag-cmp {
    flex-direction: column;
    margin-top: 2rem; // Reduced margin for smartphones

    p {
      width: 100%;
      text-transform: uppercase;
      text-align: right;
    }
  }

  span {
    font-size: 2rem; // Reduced font size for smartphones

    @media screen and (min-width: 2000px) {
      font-size: 5rem;
    }
  }

  h1 {
    font-size: 1.5rem; // Adjust this to fit your needs
  }

  @media screen and (max-width: 1200px) {
    justify-content: flex-start;
    align-items: flex-start;
  }

  @media screen and (max-width: 450px) {
    justify-content: center;
    align-items: center;
    text-align: center;

    .tag-cmp {
      margin-top: 1rem; // Further reduced margin for smaller screens
    }

    p,
    h1 {
      font-size: 1.2rem; // Further reduction for smaller screens
    }
  }
}

.app__header-circles {
  flex: 0.75;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 100%;
  margin-left: 1rem;

  div {
    border-radius: 50%;
    background: var(--white-color);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);

    img {
      width: 60%;
      height: 60%;
    }
  }

  div:nth-child(1) {
    width: 100px;
    height: 100px;
  }

  div:nth-child(2) {
    margin: 1.75rem;
    width: 150px;
    height: 150px;
  }

  div:nth-child(3) {
    width: 70px;
    height: 70px;
  }

  @media screen and (min-width: 2000px) {
    div:nth-child(2) {
      width: 400px;
      height: 400px;
    }

    div:nth-child(3) {
      width: 170px;
      height: 170px;
    }

    div:nth-child(1) {
      width: 200px;
      height: 200px;
    }
  }

  @media screen and (max-width: 1200px) {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: 0;

    div {
      margin: 1rem;
    }
  }

  @media screen and (max-width: 450px) {
    div {
      width: 60px;  // Further reduced size for smaller screens
      height: 60px;
      
      &:nth-child(2) {
        width: 90px;
        height: 90px;
      }
      
      &:nth-child(1) {
        width: 50px;
        height: 50px;
      }
      
      &:nth-child(3) {
        width: 40px;
        height: 40px;
      }

      img {
        width: 50%;  // Adjust the inner image size
        height: 50%;
      }
    }
  }
}

.app__header-img {
  flex: 1;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  position: relative;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    z-index: 1;
    border-radius: 50%;
  }

  .overlay_circle {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 90%;
  }

  @media screen and (max-width: 1200px) {
    margin-top: 1rem;
    justify-content: center;
  }

  @media screen and (max-width: 450px) {
    img {
      width: 80%; // Adjust this value as needed
      height: auto;
      object-fit: cover; // Makes sure the image covers the container properly
    }
  }
}
